<template>
    <v-container>


        <input class="search" type="text" v-model="pesquisa" required  v-on:keyup.enter="procura(pesquisa)">
        <v-btn icon @click="procura(pesquisa)">
            <v-icon>
                mdi-magnify
            </v-icon>
        </v-btn>

        <br>
        <br>

        <div class="row">
            
            <!-- Lista Produtos -->

            <v-snackbar v-model="snackbar" :multi-line="multiLine">
                {{ text }}
                <template v-slot:action="{ attrs }">
                    <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
                        Close
                    </v-btn>
                </template>
            </v-snackbar>

            <div class="colunaProduct">
                <v-card max-width="500" class="mx-auto my-12" v-for="(item, index) in info" :key="index">
                    <v-card-title>
                        {{item.data[0].title}}
                    </v-card-title>
                    <v-card-text>
                        <v-row>
                            {{item.data[0].description}}
                        </v-row>
                    </v-card-text>
                    <v-img height="150" :src="item.links[0].href"></v-img>

                    <v-card-actions>
                        <v-btn color="deep-purple " text @click="favorito(item)">
                            Guardar como Favorito
                        </v-btn>
                    </v-card-actions>
                </v-card >
            </div>

    <!-- Lista favoritos -->

            <div class="colunaFav">
                <div v-if="favoritos.length >0">
                    <h3>
                        Favoritos
                    </h3>
                    <div v-for="(fav, index) in favoritos" :key="index">
                        {{fav.data[0].title}} <v-icon x-small @click="removeFav(index)">mdi-close-circle-outline</v-icon>
                    </div>
                </div>
            </div>

        </div>
   </v-container>
</template>

<style>
    .row {
        display: flex;
    }

    .colunaProduct {
        flex: 70%;
        padding: 10px;
    }

    .colunaFav {
        flex: 30%;
        padding: 10px;
    }

    .search[type=text] {
        border: 1px solid grey;
        border-radius: 4px;
        background-color: white;
    }
    .buttom_search{
        background-color: white;
    }
</style>



<script>
import axios from "axios";

export default {
  data() {
    return {
      info: null,
      favoritos: [],
      vertical: true,
      multiLine: true,
      snackbar: false,
      text: `Esse item jÃ¡ foi adicionado aos favoritos!`,
    };
  },
  mounted() {
    var that = this;
    axios
      .get("https://images-api.nasa.gov/search?q=earth&media_type=image")
      .then(response => (this.info = response.data.collection.items));
    console.log(that.info);
  },
  
  methods: {
    favorito(item) {
        if (this.favoritos.indexOf(item) === -1 ){
            this.favoritos.push(item);
        } else {
            this.snackbar = true;
        }

 
      console.log(this.favoritos);
    },
    removeFav(item){
        this.favoritos.splice (item,1)
    },
    procura(pesquisa){
        axios
        .get("https://images-api.nasa.gov/search?q=" + pesquisa + "&media_type=image")
        .then(response =>(this.info = response.data.collection.items));

    },
  },
};
</script>